@import 'base';

#search_form .input { width:320px; padding:5px; vertical-align: top; }
#search_form select { width:140px; vertical-align: top; }
#search_form .button-submit { padding: 6px 10px; vertical-align: top; }

#search_results_pills { margin:20px 0; }

#search_results_list .item .field {  margin-bottom:5px;  }

#search_results_list .field em,
#search_results_list .media-heading a em { color:#c0392b; }
#search_results_list .media + .media {
    margin-top: 1rem;
}